import { Block, BlockChildren } from '@ali/4ever-cangjie';
import type { ParagraphProperties, ParagraphData, ParagraphChildren } from '@ali/4ever-plugin-paragraph';

export type SubjectName = 'review' | 'topic' | 'conclusion';

export interface SubjectClock {
  duration?: number;
  start?: number;
  pause?: number;
  end?: number;
  enableVolume?: boolean;
}

/**
 * 议题属性
 */
export interface SubjectProperties {
  /**
   * 议题 ID
   */
  id?: string;
  /**
   * 议题名（类型）
   */
  name?: SubjectName;
  /**
   * 计时器
   */
  clock?: SubjectClock;
  /**
   * 是否完成填报
   */
  finished?: boolean;
}

export interface FoldableHeadingData extends ParagraphData {
  /**
   * 标题折叠
   */
  fold?: boolean;
}

export interface Heading1Data extends FoldableHeadingData {
  /**
   * 议题属性
   */
  subjectPr?: SubjectProperties;
}

export default class Heading1 extends Block<Heading1Data, ParagraphChildren> {
  static TYPE = 'heading-1';

  static create<SD = ParagraphData, SN extends BlockChildren = ParagraphChildren>(
    properties: Heading1 | ParagraphProperties<SD, SN> | string,
  ) {
    if (typeof properties === 'string') {
      throw new Error('`Heading1.create` does not accept string');
    }
    if (Heading1.isHeading1(properties)) {
      return properties as Block<SD, SN>;
    }
    return super.create<SD, SN>({
      ...properties,
      type: Heading1.TYPE,
    });
  }

  static isHeading1(maybeHeading1: any): maybeHeading1 is Heading1 {
    return Block.isBlock(maybeHeading1) && maybeHeading1.type === Heading1.TYPE;
  }
}
